.App {
  margin: 0 auto;
  color: #f5eded;
  max-width: 1200px;
}

.container {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.card {
  width: 300px;
  height: 300px;
  padding: 1em;
  margin: 1em;
  background-color: #3e3636;
  border-radius: 12px;
}

.card p {
  font-weight: bold;
}

.en-stock {
  color: greenyellow;
}

.timer {
  color: greenyellow;
}

.image {
  border-radius: 5px;
  width: auto;
  object-fit: cover;
}

.en-rupture {
  color: #d72323;
}

.title {
  text-align: left;
  color: #f5eded;
  padding: 1em;
}

.title-timer {
  text-align: left;
  padding-left: 1.5em;
}

.gclink {
  text-decoration: none;
  color: #f5eded;
}

.gc-selector {
  margin-left: 1.5em;
  margin-top: 1.5em;
  width: 100%;
  min-width: 15ch;
  max-width: 30ch;
  border: 1px solid var(--select-border);
  border-radius: 0.25em;
  padding: 0.25em 0.5em;
  font-size: 1.25rem;
  cursor: pointer;
  line-height: 1.1;
  background-color: #fff;
  background-image: linear-gradient(to top, #f9f9f9, #fff 33%);
}
